.row-style {
  flex: 1 1 40%;
  padding: 1% 5%;
  box-sizing: border-box;
}

.dim-style {
  flex: 1 1 25%;
  padding: 1% 1%;
  box-sizing: border-box;
}

.dim-style2 {
  flex: 1 1 25%;
  padding: 1% 1%;
  box-sizing: border-box;
  max-width: 22%;
}

::ng-deep .p-dialog-footer {
  padding: 1.5rem;
}

.requiredField {
  color: red;
  font-size: 18px;
  line-height: 10px;
  position: relative;
  top: -1px;
  display: inline-block;
}

.error-msg {
  color: red;
  font-size: 12px;
  margin-top: 2px;
  margin-bottom: 2px;
}


.equipment-heading {
  width: 100%;
  text-align: center;
  padding: 15px;
}

.component-type-header {
  display: flex;
  flex-wrap: wrap;
}

.dim-upper-style{
 display: flex; 
 flex-wrap: wrap;
}

.confirmParent{
  width:100%;
  text-align:center;
}

.file-upload {
  width: 100%;
  max-width: 100%;
  height: 100px;
  border: 2px dashed #007bff;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #666;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin: 20px auto;

  &:hover,
  &.dragover {
    background-color: #f0f8ff;
    border-color: #0056b3;
  }
}

.task-dialog-content {
  padding-top: 30px;
  .p-inputtext {
    width: 100%;
  }
  
  .p-dropdown {
    width: 100%;
  }
  
  .p-calendar {
    width: 100%;
  }
  
  .p-inputtextarea {
    width: 100%;
  }
}

.upload-btn-small {
  padding: 0.25rem 0.75rem !important;
  font-size: 0.85rem !important;
  min-width: unset !important;
  width: auto !important;
  height: 2rem !important;
  box-sizing: border-box !important;
}